<template>
    <div id="app">
        <b-modal :active.sync="isAdbActive" trap-focus v-if="isAdbActive">
            <div class="modal-card" style="width: auto">
                <header class="modal-card-head">
                    <p class="modal-card-title">与Adb设备连接</p>
                </header>
                <section class="modal-card-body">
                    <b-steps
                            v-model="step"
                            :animated=true
							:hasNavigation=false

                    >
                        <b-step-item label="Welcome" :clickable=false>
                            <h1 class="title has-text-centered">Welcome</h1>
                            <p>点击下方 Connect 来连接你的Adb设备.</p>
                            <p><b>Note：<br>你需要Chrome 61以上内核来支持WebUSB.</b></p>
                            <b-message title="Error!" v-if="errors.length!==0" type="is-danger" style="width: 50%;margin-left: 25%;margin-right: 25%"
                                       aria-close-label="Close message" has-icon>
                               <center>{{errors}}</center>
                            </b-message>
                            <b-button type="is-primary" @click="connect" outlined>Connect</b-button>
                        </b-step-item>

                        <b-step-item label="Auth" :clickable=false :type="{'is-success': isAuthSuccess}" disabled>
                            <h1 class="title has-text-centered">Auth</h1>
                            <p v-if="isAuthSuccess===false">请在{{usb.device['manufacturerName']}} {{usb.device['productName']}}设备上授权</p>
                            <p v-if="isAuthSuccess">恭喜，您已完成授权！</p>
                            <b-button type="is-success" @click="step=2" v-if="isAuthSuccess" outlined>Next
                            </b-button>
                        </b-step-item>

                        <b-step-item  label="Done" :clickable=false disabled  :type="{'is-success': true}">
                            <h1 class="title has-text-centered">Done</h1>
                           <p>已完成！</p>
                        </b-step-item>


                    </b-steps>
                </section>
            </div>

        </b-modal>
		<b-modal :active.sync="isModalActive">
			<div class="modal-card" style="width: auto">
				<header class="modal-card-head">
					<p class="modal-card-title">{{current.name}}</p>
				</header>
				<section class="modal-card-body">
					<div v-if="current.type===1">
						<b-field label="Required" v-if="current.content.length>1">
							<b-select placeholder="选择一个选项" required v-model="current.choice">

								<option :value="con.command" v-for="con in current.content">{{con.name}}</option>
							</b-select>
						</b-field>
						<p v-if="current.choice!==''">
							即将执行ADB：adb shell {{current.choice}}

						</p>
						<b-button type="is-warning" @click="execute_usb(current.choice)">执行</b-button>
					</div>
					<div v-if="current.type===2">
						<b-field>
							<b-upload v-model="dropFiles"
									  drag-drop>
								<section class="section">
									<div class="content has-text-centered">
										<p>
											<b-icon
													icon="upload"
													size="is-large">
											</b-icon>
										</p>
										<p v-if="!dropFiles">拖拽或点击选择安装包</p>
										<div class="tags" v-else>
            <span>
                {{dropFiles.name}}

            </span>
										</div>
									</div>

								</section>
							</b-upload>
						</b-field>

						<p> <b-button type="is-warning" @click="installApk">安装</b-button></p>
					</div>
					<div v-if="current.type===3">
						<b-field label="Rounded">
							<b-input placeholder="笔给你，需要什么自己来！" rounded v-model="shell"></b-input>
						</b-field>
						<p> <b-button type="is-warning" @click="execute_usb(shell)" v-if="shell.length!==0">我 好 了</b-button></p>
					</div>
					<b-message title="Error!" v-if="errors.length!==0" type="is-danger" style="width: 50%;margin-left: 25%;margin-right: 25%"
							   aria-close-label="Close message" has-icon>
						<center>{{errors}}</center>
					</b-message>
				</section></div>


		</b-modal>
		<div style="height: 80vh;display: flex;">
			<div style="margin: 20%">
			<div class="tile is-ancestor" style="justify-content: center">
				<div class="tile is-parent is-vertical is-4">
					<article class="tile is-child notification is-primary" @click="showadb">
						<p class="title">ADB激活</p>
						<p class="subtitle">呐呐呐你可以在网页上激活Brevent啦，空调狗和 Shizuku Manager也是一样嗷</p>
					</article>
					<article class="tile is-child notification is-warning" @click="showadw">
						<p class="title">设备管理员</p>
						<p class="subtitle">If 你想使用设备管理员模式的冰箱:</p>
						<p class="subtitle">        this.click()</p>
					</article>

				</div>
				<div class="tile is-parent is-vertical is-4">
					<article class="tile is-child notification is-warning" @click="showapk">
						<p class="title">安装Apk</p>
						<p class="subtitle">安装本地Apk</p>
					</article>
					<article class="tile is-child notification is-warning" @click="showself">
						<p class="title">执行自定义命令</p>
						<p class="subtitle">笔给你，自己写！</p>
					</article>
				</div>
			</div>
		</div></div>
    </div>
</template>

<script>
    import Adb from "webadb"

    export default {
        name: 'app',
        //  mounted() {

        //  },
        data() {
            return {
                name: "",
                step: 0, isAdbActive: true, adb: null, isAuthSuccess: undefined, errors: "", usb: null,isModalActive:false,current:{
                	"type":0,"name":"",content:[{"name":"","command":""}],choice:""
				},dropFiles:undefined,shell:"",commandlist:[{'name':'brevent.sh','command':'sh /data/data/me.piebridge.brevent/brevent.sh'
				},{'name':'冰箱ADB模式','command':'sh /sdcard/Android/data/com.catchingnow.icebox/files/start.sh'},{'name':'Shizuku Manager 激活','command':'shell sh /sdcard/Android/data/moe.shizuku.privileged.api/files/start.sh'},{'name':"小黑屋（麦克斯韦妖）",'command':'sh /sdcard/Android/data/web1n.stopapp/files/demon.sh'},{'name':'权限狗','command':"sh /sdcard/Android/data/com.web1n.permissiondog/files/starter.sh"}]
            }
        }, methods: {
        	showadb(){
        		this.errors=""
				this.current={'type':1,'name':'ADB激活',content: this.commandlist}
				this.isModalActive=true;

					},showadw(){        		this.errors=""

				this.current={'type':1,'name':'设备管理员激活',content:[{"name":"小黑屋","command":"dpm set-device-owner com.catchingnow.icebox/.receiver.DPMReceiver"},{"name":"小黑屋","command":"dpm set-device-owner web1n.stopapp/.receiver.AdminReceiver"}]}
				this.isModalActive=true;

			},showapk(){        		this.errors=""
this.dropFiles=undefined
				this.current={'type':2,'name':'安装自定义APK'}
				this.isModalActive=true;

			},showself(){        		this.errors=""

				this.current={'type':3,'name':'自力更生'}
				this.isModalActive=true;
			},
            async connect() {
                try {
					if(!navigator.usb) {
						throw new Error("浏览器不支持WebUSB");

				}
                    this.usb = await Adb.open('WebUSB');
                    if (!this.usb || !(this.usb.isAdb() || this.usb.isFastboot()))
					{throw new Error("设备不在Adb或Fastboot模式");}
                    this.step = 1;
                } catch (error) {
                    console.log(error);
                    this.errors = error.toString();
                    return

                }
                if (this.usb.isAdb()) {

                    this.adb = await this.usb.connectAdb('host::',()=>{
                    	this.isAuthSuccess=false
					});
                    if (this.adb != null) {
                        this.isAuthSuccess = true

                    }


                }
            },
			xfer_stats(start_time, done, total, output)
	{
		let now = Date.now();

		if (now - xfer_stats_time < 500)
			return;

		if (xfer_stats_done > done)
			xfer_stats_done = 0;
		if (xfer_stats_time < start_time)
			xfer_stats_time = start_time;

		let delta = Math.round((now - start_time) / 1000);
		let instant = Math.round(((done - xfer_stats_done) * 1000) / ((now - xfer_stats_time) * 1024));
		let average = Math.round(done * 1000 / ((now - start_time) * 1024));

		xfer_stats_done = done;
		xfer_stats_time = now;

		let out = "";
		out += Math.round(100 * done / total) + "% (";
		out += Math.round(done / 1024) + " KiB in ~" + delta + " secs at avg " + average + " KiB/s, cur " + instant + " KiB/s)";
		output(out);
	},
			async push_usb(dest)
			{

				this.errors=""

				try {
					if (this.adb != null ) {
						this.$buefy.snackbar.open({message:"Loading " + this.dropFiles.name + "...",position:"is-top"})
						let sync = await this.adb.sync();
						let start_time = Date.now();
						await sync.push(this.dropFiles, dest,"0644",(done, total) => {console.log(done,total)});

						await sync.quit();
						sync = null;
					}
				}
				catch(error) {
					console.log(error);
				}
			},
			async installApk(){
				try {

				if (!this.adb) {
					throw new Error("ADB设备未连接")
				}else {
					console.log(this.dropFiles)
					if (!this.dropFiles) {
						throw new Error("无文件")

					} else {
						let filenamestr=new Date().getSeconds().valueOf()+".apk"
						console.log(filenamestr)
						await this.push_usb("/data/local/tmp/"+filenamestr)
						await this.execute_usb("pm install -r /data/local/tmp/"+filenamestr)
						this.$buefy.snackbar.open({message:"安装" + this.dropFiles.name + "完成",position:"is-top"})



					}
				}	}	catch (e) {
					console.log(e)
					this.errors=e.message
					this.$buefy.snackbar.open({
						duration: 5000,
						message: e.message,
						type: 'is-danger',
						position: 'is-top',
						queue: false,


					})
				}
				}

			,
			async execute_usb(command)
			{let decoder = new TextDecoder();

				this.errors=""



				try {
					if (this.adb == null ) {
					throw new Error("ADB设备未连接")

				}


					let shell = await this.adb.open("shell: "+command);
					let r = await shell.receive();
					while (r.cmd == "WRTE") {
						if (r.data != null) {

								this.$buefy.snackbar.open({message:decoder.decode(r.data),position:'is-top'})


						}

						shell.send("OKAY");
						r = await shell.receive();
					}

					shell.close();
					shell = null;
				} catch (error) {
					this.errors=error.message
					this.$buefy.snackbar.open({
						duration: 5000,
						message: error.message,
						type: 'is-danger',
						position: 'is-top',
						queue: false,


					})
				}

				}}






    }
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }
</style>
